<template>
  <div class="klk-cropper">
    <img :id="id" :src="src" />
  </div>
</template>
<script>
import Cropper from 'cropperjs'
import 'cropperjs/dist/cropper.css'
import _ from 'lodash'

export default {
  name: 'Cropper',
  props: {
    // 
    id: {
      type: String,
      default: () => _.uniqueId('klk_cropper_')
    },
    // src  base64, dataURI, 
    src: {
      type: String,
      default: ''
    },
    /**
     * 0: 
     * 1: 
     * 2: , 
     * 3: 
     * 23
     */
    viewMode: {
      type: Number,
      default: 0
    },
    /**
     * 
     * crop: 
     * move: 
     */
    dragMode: {
      type: String,
      default: 'crop'
    },
    /**
     * , , 
     */
    initialAspectRatio: {
      type: Number,
      default: undefined
    },
    /**
     * , , 
     */
    aspectRatio: {
      type: Number,
      default: undefined
    },
    /**
     * , x,y,w,h
     */
    data: {
      type: Object,
      default: null
    },
    preview: {
      type: [Object, String],
      default: ''
    },
    /**
     * 
     */
    responsive: {
      type: Boolean,
      default: true
    },
    // 
    restore: {
      type: Boolean,
      default: true
    },
    // 
    checkCrossOrigin: {
      type: Boolean,
      default: true
    },
    checkOrientation: {
      type: Boolean,
      default: true
    },
    // 
    modal: {
      type: Boolean,
      default: true
    },
    // 
    guides: {
      type: Boolean,
      default: true
    },
    // 
    center: {
      type: Boolean,
      default: true
    },
    // , 
    highlight: {
      type: Boolean,
      default: true
    },
    // 
    background: {
      type: Boolean,
      default: true
    },
    // 
    autoCrop: {
      type: Boolean,
      default: true
    },
    //  01, 
    autoCropArea: {
      type: Number,
      default: 0.8
    },
    // 
    movable: {
      type: Boolean,
      default: true
    },
    // 
    rotatable: {
      type: Boolean,
      default: true
    },
    // 
    scalable: {
      type: Boolean,
      default: true
    },
    // 
    zoomable: {
      type: Boolean,
      default: true
    },
    // 
    zoomOnTouch: {
      type: Boolean,
      default: true
    },
    // 
    zoomOnWheel: {
      type: Boolean,
      default: true
    },
    // 
    wheelZoomRatio: {
      type: Number,
      default: 0.1
    },
    // 
    cropBoxMovable: {
      type: Boolean,
      default: true
    },
    // 
    cropBoxResizable: {
      type: Boolean,
      default: true
    },
    //   or 
    toggleDragModeOnDblclick: {
      type: Boolean,
      default: true
    },

    //  ---------------------------
    // 
    minCanvasWidth: {
      type: Number,
      default: 0
    },
    // 
    minCanvasHeight: {
      type: Number,
      default: 0
    },
    // 
    minCropBoxWidth: {
      type: Number,
      default: 0
    },
    // 
    minCropBoxHeight: {
      type: Number,
      default: 0
    },
    // 
    minContainerWidth: {
      type: Number,
      default: 200
    },
    // 
    minContainerHeight: {
      type: Number,
      default: 100
    },

    //  ---------------------------
    // 
    ready: {
      type: Function,
      default: () => {}
    },
    // 
    cropstart: {
      type: Function,
      default: () => {}
    },
    // 
    cropmove: {
      type: Function,
      default: () => {}
    },
    // 
    cropend: {
      type: Function,
      default: () => {}
    },
    // 
    crop: {
      type: Function,
      default: () => {}
    },
    // 
    zoom: {
      type: Function,
      default: () => {}
    }
  },
  computed: {
    // 
    scalingRatio() {
      let imgData = this.cropper.getImageData()
      return imgData.width / imgData.naturalWidth
    }
  },
  watch: {
    // src, 
    src(next, old) {
      next !== old && this.replace(next)
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    // 
    init() {
      const image = document.getElementById(this.id)
      this.cropper = new Cropper(image, this.$props)
    },
    // 
    reset() {
      return this.cropper.reset()
    },

    // 
    clear() {
      return this.cropper.clear()
    },

    // 
    cropp() {
      return this.cropper.crop()
    },

    /**
     * src
     * @param {string} src - 
     * @param {boolean} [hasSameSize] - true, cropper
     * @returns {Object} cropper
     */
    replace(src, hasSameSize = false) {
      return this.cropper.replace(src, hasSameSize)
    },

    // 
    enable() {
      return this.cropper.enable()
    },

    //  disable
    disable() {
      return this.cropper.disable()
    },

    // 
    destroy() {
      return this.cropper.destroy()
    },

    /**
     * 
     * @param {number} offsetX x
     * @param {number} offsetY y
     * @returns {Object} cropper
     */
    move(offsetX, offsetY) {
      return this.cropper.move(offsetX, offsetY)
    },

    /**
     * 
     * @param {number} x x
     * @param {number} [y=x] y
     * @returns {Object} cropper
     */
    moveTo(x, y = x) {
      return this.cropper.moveTo(x, y)
    },

    /**
     * ()
     * @param {number} ratio ()
     * @param {Event} _originalEvent 
     * @returns {Object} cropper
     */
    relativeZoom(ratio, _originalEvent) {
      return this.cropper.zoom(ratio, _originalEvent)
    },

    /**
     * 
     * @param {number} ratio - ()
     * @param {Event} _originalEvent 
     * @returns {Object} cropper
     */
    zoomTo(ratio, _originalEvent) {
      return this.cropper.zoomTo(ratio, _originalEvent)
    },

    /**
     * ()
     * @param {number} degree ()
     * @returns {Object} cropper
     */
    rotate(degree) {
      return this.cropper.rotate(degree)
    },

    /**
     * 
     * @param {number} degree - ()
     * @returns {Object} cropper
     */
    rotateTo(degree) {
      return this.cropper.rotateTo(degree)
    },

    /**
     *  X
     * @param {number} scaleX
     * @returns {Object} cropper
     */
    scaleX(scaleX) {
      return this.cropper.scaleX(scaleX)
    },

    /**
     *  Y
     * @param {number} scaleY
     * @returns {Object} cropper
     */
    scaleY(scaleY) {
      return this.cropper.scaleY(scaleY)
    },

    /**
     * 
     * @param {number} scaleX
     * @param {number} [scaleY=scaleX]
     * @returns {Object} cropper
     */
    scale(scaleX, scaleY = scaleX) {
      return this.cropper.scale(scaleX, scaleY)
    },

    /**
     * 
     * @param {boolean} [rounded=false] - 
     * @returns {Object} 
     * x: 
     * y: 
     * width: 
     * height: 
     * rotate: 
     */
    getData(rounded = false) {
      return this.cropper.getData(rounded)
    },

    /**
     * 
     * @param {Object} data - getData
     * @returns {Object} cropper
     */
    setData(data) {
      return this.cropper.setData(data)
    },

    /**
     * 
     * @returns {Object}
     * width: 
     * height: 
     */
    getContainerData() {
      return this.cropper.getContainerData()
    },

    /**
     * 
     * @returns {Object}
     * left: the offset left of the image
     * top: the offset top of the image
     * width: the width of the image
     * height: the height of the image
     * naturalWidth: the natural width of the image
     * naturalHeight: the natural height of the image
     * aspectRatio: the aspect ratio of the image
     * rotate: the rotated degrees of the image if rotated
     * scaleX: the scaling factor to apply on the abscissa of the image if scaled
     * scaleY: the scaling factor to apply on the ordinate of the image if scaled
     */
    getImageData() {
      return this.cropper.getImageData()
    },

    /**
     * canvas 
     * @returns {Object}
     */
    getCanvasData() {
      return this.cropper.getCanvasData()
    },

    /**
     * Set the canvas position and size with new data.
     * @param {Object} data - The new canvas data.
     * @returns {Object} cropper
     */
    setCanvasData(data) {
      return this.cropper.setCanvasData(data)
    },

    /**
     * 
     * @returns {Object}
     * left: the offset left of the crop box
     * top: the offset top of the crop box
     * width: the width of the crop box
     * height: the height of the crop box
     */
    getCropBoxData() {
      return this.cropper.getCropBoxData()
    },

    /**
     * Set the crop box position and size with new data.
     * @param {Object} data - The new crop box data.
     * @returns {Object} cropper
     */
    setCropBoxData(data) {
      return this.cropper.setCropBoxData(data)
    },

    /**
     * 
     * @param {Object} [options={}] - The config options.
     * width: the destination width of the output canvas.
     * height: the destination height of the output canvas.
     * minWidth: the minimum destination width of the output canvas, the default value is 0.
     * minHeight: the minimum destination height of the output canvas, the default value is 0.
     * maxWidth: the maximum destination width of the output canvas, the default value is Infinity.
     * maxHeight: the maximum destination height of the output canvas, the default value is Infinity.
     * fillColor: a color to fill any alpha values in the output canvas, the default value is transparent.
     * imageSmoothingEnabled: set to change if images are smoothed (true, default) or not (false).
     * imageSmoothingQuality: set the quality of image smoothing, one of "low" (default), "medium", or "high".
     * @returns {HTMLCanvasElement} - The result canvas.
     */
    getCroppedCanvas(options = {}) {
      return this.cropper.getCroppedCanvas(options)
    },

    /**
     * 
     * @param {number} aspectRatio
     * @returns {Object} cropper
     */
    setAspectRatio(aspectRatio) {
      return this.cropper.setAspectRatio(aspectRatio)
    },

    /**
     * or
     * @param {string} mode - move | drag
     * @returns {Object} cropper
     */
    setDragMode(mode) {
      return this.cropper.setDragMode(mode)
    },
    /**
     *  base64 String
     * @param {string} mime -  | image/webp | image/jpg | image/jpeg | image/png
     * @returns {string} base64
     */
    getDataURI(mime, quality = 1) {
      let cvs = this.getCroppedCanvas()
      cvs.toDataURL(mime, quality)
    },
    /**
     * ?   Blob ?  URL ?
     * @param {string} mime -  | image/webp | image/jpg | image/jpeg | image/png
     * @returns {string} Blob or URL
     */
    async getBlobURL(mime, quality = 1) {
      let cvs = this.getCroppedCanvas()
      let objURL = await new Promise((resolve) => {
        cvs.toBlob(
          (blob) => {
            resolve(URL.createObjectURL(blob))
          },
          mime,
          quality
        )
      })
      return objURL
    }
  }
}
</script>
<style lang="scss">
.klk-cropper {
  width: 100%;
  height: 100%;
  img {
    display: block;
    max-width: 100%;
  }
  .cropper-line,
  .cropper-point {
    background-color: transparent;
  }
  .cropper-modal {
    opacity: 0.7;
  }
}
</style>
