<!-- :  -->
<template>
  <div class="activity-blocklist">
    <a-form-model-item
      v-show="!isMerchant"
      class="common-reset-style"
      :label="
        $t('act_blocklist_publish_conditions') +
          ' - ' +
          $t('act_blocklist_language_availability')
      "
    >
      <span
        class="tips gray"
        style="display: block; line-height: 14px; color: #aaa"
      >
        {{ $t('act_blocklist_note') }}
      </span>
      <a-tooltip
        placement="right"
        overlayClassName="common-tooltip-style"
        :title="$t('language_publish_condition')"
      >
        <a-radio-group v-model="selected_type" @change="onSelectedTypeChange">
          <a-radio value="all">
            {{ $t('act_blocklist_all_languages') }}
          </a-radio>
          <a-radio value="part">
            {{ $t('act_blocklist_selected_languages') }}
          </a-radio>
        </a-radio-group>
      </a-tooltip>
      <template v-if="selected_type === 'part'">
        <!-- <span class="tips gray">
            Note: Unselected locale(s) will be added to the blocklist and stay
            offline. You can come back to modify the setting anytime.
          </span> -->
        <!-- <br /> -->
        <a style="cursor: pointer" @click="selectAll">
          {{ $t('act_blocklist_select_all') }}
        </a>
        -
        <a style="cursor: pointer" @click="clearAll">
          {{ $t('act_blocklist_clear_all') }}
        </a>
        <div class="checkbox-wrap">
          <a-checkbox-group
            v-for="(options, key) of languages"
            :key="key"
            v-model="selected_languages[key]"
          >
            <a-checkbox
              v-for="lang in options"
              :key="lang.val"
              :value="lang.val"
            >
              {{ lang.name }}
            </a-checkbox>
          </a-checkbox-group>
        </div>
      </template>
    </a-form-model-item>
    <a-modal
      :visible.sync="dialogVisible"
      size="tiny"
      :title="$t('global_tips')"
      :maskClosable="false"
      @cancel="handleCancel"
    >
      <div class="warning-message">
        <template v-if="modified_data.added.length">
          <div style="font-weight: 500">
            {{
              $t('act_blocklist_please_confirm') +
                $t('act_blocklist_system_will_notify')
            }}
          </div>
          <div v-for="lang in modified_data.added" :key="lang">
            - {{ lang_obj[lang] }}
          </div>
        </template>
        <template v-if="modified_data.moved.length">
          <div style="font-weight: 500">
            {{
              $t('act_blocklist_not_publish') +
                $t('act_blocklist_will_not_publish')
            }}
          </div>
          <div v-for="lang in modified_data.moved" :key="lang">
            - {{ lang_obj[lang] }}
          </div>
        </template>
        <!-- <br /> -->
        <!-- <div
          style="font-weight: 500"
          v-if="modified_data.moved.length && modified_data.added.length"
        >
          It will re-trigger the onboarding workflow.
        </div> -->
      </div>
      <span slot="footer">
        <a-button @click="handleCancel">
          {{ $t('global_cancel') }}
        </a-button>
        <a-button type="primary" @click="handleConfirm">
          {{ $t('global_confirm') }}
        </a-button>
      </span>
    </a-modal>
  </div>
</template>
<script>
import { isMerchant } from '@/env'
// 
const lang_conf = require('lang_conf')
// 
const lang_array = lang_conf.getAllLangOption().map((v) => ({
  name: v.options,
  val: v.B_LANG
}))
// key: B_LANG, value: LANG_TITLE
const lang_obj = lang_conf.getLangObj('B_LANG', 'LANG_TITLE')
console.log('all-language', JSON.stringify(lang_array))

// 
export default {
  name: 'ActivityBlocklist',
  props: {
    //!! v-model: 
    value: {
      type: Array,
      default: () => []
    },
    //! ID
    activityId: {
      type: Number,
      default: NaN
    }
  },
  inject: ['getBasicInfoResult2provide'],
  watch: {
    '$attrs.result': {
      handler({ publish_conditions }) {
        this.publish_conditions = publish_conditions || []
        this.init()
      }
    }
  },
  data() {
    return {
      isMerchant,
      publish_conditions: [],
      //!  or 
      selected_type: 'all', // all / part
      //!  //
      languages: {
        en: [],
        zh: [],
        other: []
      },
      //! 
      selected_languages: {
        zh: [],
        en: [],
        other: []
      },
      lang_obj,
      //!  or , 
      old_data: lang_array.map((v) => v.val),
      //! , true , false 
      isCreated: !_.isEmpty(klook.urlParam('lang')),
      dialogVisible: false,
      // 
      modified_data: {
        added: [],
        moved: []
      },
      saveSucCallback: () => {}
    }
  },
  created() {
    // 
    lang_array.forEach((v) => {
      const { val } = v
      let is_other = true
      for (const key in this.languages) {
        // 
        if (val.includes(key)) {
          is_other = false
          this.languages[key].push(v)
        }
      }
      //  zh || en ,  other
      if (is_other) {
        this.languages.other.push(v)
      }
    })
    // 
    // this.init()
  },
  methods: {
    /**
     * !
     * 1. :  all locales 
     * 2. :  blocklist
     */
    async init() {
      if (this.initFirst) return
      this.initFirst = true
      // 
      if (!this.isCreated) {
        // 
        this.selectAll()
        this.selected_type = 'all'
        this.cacheData = _.cloneDeep(this.selected_languages)
        return
      }
      // 
      let { publish_conditions: res } = this
      if (res) {
        this.selected_type = 'part'
        const all_items = _.merge([], res || [])
        // 
        const langs = (all_items.filter((v) => !v.status) || []).map(
          (v) => v.language
        )
        this.old_data = langs
        // ,  all locales
        if (langs.length === lang_array.length) {
          this.selected_type = 'all'
        }
        // 
        all_items.forEach((v) => {
          const { status, language } = v
          // status: 0 , 1 block
          if (status) {
            return
          }
          let flag = true
          for (const key in this.selected_languages) {
            // 
            if (language.includes(key)) {
              flag = false
              this.selected_languages[key].push(language)
            }
          }
          //  zh || en ,  other
          flag && this.selected_languages.other.push(language)
        })
      } else {
        //! , 
        this.selected_type = 'all'
        this.selectAll()
      }
      // 
      this.cacheData = _.cloneDeep(this.selected_languages)
    },
    // 
    clearAll() {
      for (let key in this.selected_languages) {
        this.selected_languages[key] = []
      }
    },
    // 
    selectAll() {
      for (let key in this.selected_languages) {
        this.selected_languages[key] = this.languages[key].map((v) => v.val)
      }
    },
    //  blocklist
    // modify_id:buttonmodify id.
    save() {
      // 
      const data = {}
      if (this.selected_type === 'all') {
        // 
        data.language_list = []
        data.status = 1 //  0-, 1-,
      } else {
        // , language_liststatus
        data.language_list = Object.values(this.selected_languages).flat()
        data.status = 0 //  0-, 1-,
        //! block, 
        // await this.checkSelectedLanguage(data.language_list);
      }

      // basic info
      this.saveSucCallback = (res) => {
        if (res) {
          this.old_data = Object.values(this.selected_languages).flat()
          this.cacheData = _.cloneDeep(this.selected_languages)
        }
      }
      return data
    },
    //! block, 
    async checkSelectedLanguage(selected_list) {
      const resp = this.getBasicInfoResult2provide().activity_language_status
      const languages_status_all = _.get(resp, 'status') || []
      //! 
      const all_published_languages = languages_status_all.reduce(
        (acc, cur) => {
          if (cur.status === 3) {
            // all_published_languages.push(cur.language);
            return [...acc, cur.language]
          }
          return acc
        },
        []
      )
      //! 
      let bool =
        !all_published_languages.length ||
        selected_list.some((v) => all_published_languages.includes(v))
      if (bool) {
        return Promise.resolve()
      }
      const h = this.$createElement
      this.$msgbox({
        title: this.$t('global_dialog_title'),
        message: h('div', null, [
          h('p', null, this.$t('act_blocklist_at_least_one'))
        ]),
        showCancelButton: false,
        confirmButtonText: this.$t('global_confirm'),
        callback: () => this.clearModifiedData()
      })
      return Promise.reject()
    },
    // 
    handleCancel() {
      this.clearModifiedData()
      this.remindPromise.reject()
    },
    // 
    handleConfirm() {
      this.clearModifiedData()
      this.remindPromise.resolve()
    },
    // 
    getChangedData() {
      // 
      const new_data = Object.values(this.selected_languages).flat()
      lang_array.forEach((v) => {
        // , , 
        if (new_data.includes(v.val) && !this.old_data.includes(v.val)) {
          this.modified_data.added.push(v.val)
        }
        // , , 
        if (!new_data.includes(v.val) && this.old_data.includes(v.val)) {
          this.modified_data.moved.push(v.val)
        }
      })
    },
    // 
    clearModifiedData() {
      this.modified_data = {
        added: [],
        moved: []
      }
      this.dialogVisible = false
    },
    // remind 
    async remind() {
      this.getChangedData()
      //! ,  resolve, 
      if (
        // this.selected_type === "part" &&
        !this.modified_data.added.length &&
        !this.modified_data.moved.length
      ) {
        return true
      }
      //! , ""
      // , language_liststatus
      let selected_languages = Object.values(this.selected_languages).flat()
      //! block, 
      await this.checkSelectedLanguage(selected_languages)
      this.dialogVisible = true
      return new Promise((resolve, reject) => {
        this.remindPromise = {
          resolve,
          reject
        }
      })
    },
    // 
    onSelectedTypeChange(val) {
      if (val === 'all') {
        this.selectAll()
      } else {
        this.selected_languages = _.cloneDeep(this.cacheData)
      }
    },
    // 
    isChanged() {
      return _.isEqual(this.cacheData, this.selected_languages)
    }
  }
}
</script>
<style lang="scss" scoped>
.activity-blocklist {
  .checkbox-wrap {
    line-height: 0;
    .ant-col.ant-form-item-control-wrapper {
      max-width: auto;
    }
    .ant-checkbox-wrapper {
      margin: 0 8px 0 0;
    }
  }
}
</style>
