import axios from 'axios'

/**
 * 
 * cloudName 
 * uploadPreset unsigned
 * tags  - 
 * folder  - 
 */
const CLOUDINARY_CONFIG = {
  cloudName: 'klook',
  uploadPreset: 'k8xu3bkg',
  tags: '',
  folder: ''
}

// url
const getUrl = (cloudName) =>
  `https://api.cloudinary.com/v1_1/${cloudName}/upload`

/**
 * 
 * @param {File | String} file  -  | remote url
 */
const uploadFile = async (file, params) => {
  let { cloudName, uploadPreset, tags, folder } = {
    cloudName: CLOUDINARY_CONFIG.cloudName,
    uploadPreset: CLOUDINARY_CONFIG.uploadPreset,
    tags: CLOUDINARY_CONFIG.tags,
    folder: CLOUDINARY_CONFIG.folder,
    max_file_size: 4000000, // 4M
    max_image_width: 3000,
    ...(params.uploadConf || {})
  }
  let url = getUrl(cloudName)
  let form = new FormData()
  form.append('upload_preset', uploadPreset)
  form.append('file', file)
  tags && form.append('tags', tags)
  folder && form.append('folder', folder)

  const config = {
    onUploadProgress: (e) =>
      typeof params.setPercentage === 'function' &&
      params.setPercentage(((e.loaded / e.total) * 100) | 0)
  }
  try {
    let res = await axios.post(url, form, config)
    if (res.status === 200) {
      console.log('')
      return res.data
    }
  } catch (e) {
    console.log('')
    throw new Error(e)
  }
}

export { uploadFile }
