import { isMerchant } from '@/env'
import Vue from 'vue'
import * as mPms from '@klook/merchant-permission'
import * as aPms from '@klook/admin-permission'
function getPmsModule() {
  let pmsModule
  pmsModule = isMerchant ? mPms : aPms
  let { checkPermission } = pmsModule
  return {
    checkPermission
  }
}
const checkPermission = getPmsModule().checkPermission

const merchantAuth = {
  actList: 'experience/act/merchant/list_page', //
  pkgManage: 'experience/act/activity_manage', // 
  delete: 'experience/act/activity_manage', //
  view: 'experience/act/activity_view', //
  edit: 'experience/act/activity_edit', //
  actSubmit: 'experience/act/merchant/submit', //
  actWithdraw: 'experience/act/merchant/submit', //
  pkgSubmit: 'experience/act/merchant/submit', //
  pkgWithdraw: 'experience/act/merchant/submit' //
}
const adminAuth = {
  actList: 'experience/act/merchant/list_page', //admin
  view: 'experience/act/activity_view', //admin
  edit: 'experience/act/activity_edit', //admin
  actApprove: 'experience/act/merchant/review', //admin approve
  actReject: 'experience/act/merchant/review', //admin reject
  pkgApprove: 'experience/act/merchant/review', //admin approve
  pkgReject: 'experience/act/merchant/review' //admin reject
}

const authMap = isMerchant ? merchantAuth : adminAuth
let permissions
let getIsAuth = (function() {
  let fn = isMerchant
    ? function(value) {
        return permissions.includes(value)
      }
    : function(value) {
        return permissions.some((pms) => pms.privilege_code === value)
      }
  return fn
})()

// 
export const checkAuth = function(value) {
  // return true //
  if (!checkPermission) return
  // let result = checkPermission(value)
  // console.log(`auth -> ${value}`, result);
  return checkPermission(authMap[value])
}
export const checkAuth2self = function(value, arrFlag = 'some') {
  // return true //
  if (!permissions) {
    permissions =
      window.userInfo2window[isMerchant ? 'permissions' : 'privileges']
    // console.log('permissions = ', permissions)
  }
  if (!value || !permissions) return true
  if (typeof value === 'string') {
    let auth = authMap[value]
    return getIsAuth(auth)
  } else if (typeof value === 'object' && value.length) {
    return value[arrFlag]((key) => getIsAuth(authMap[key]))
  }
  return true //
}

export const initAuthDirective = function() {
  Vue.directive('auth', {
    bind: function(el, binding, vnode) {
      let value = binding.value
      if (!checkAuth(value)) {
        el.style.display = 'none'
      }
    }
  })
}

export const setDisabledLoop = function(componentName, params) {
  this.$children.forEach((child) => {
    var name = child.$options.name
    if (name === componentName) {
      _.merge(child, params)
    } else {
      setDisabledLoop.apply(child, [componentName].concat([params]))
    }
  })
}

export const setMutationObserver = function(el, callback) {
  // 
  const targetNode = document.querySelector(el)
  if (!targetNode) return
  // 
  const config = { attributes: true, childList: true, subtree: true }
  // 
  const observer = new MutationObserver(callback)
  // 
  observer.observe(targetNode, config)
}
