<template>
  <!-- banner  -->
  <div>
    <a-form-model :model="form" ref="form" label-width="0" class="photo-guide">
      <a-form-model-item :required="required" :rules="getRules()" prop="custom">
        <!-- <div>
          Images uploaded in en_US will be displayed at frontend in all
          languages by default, except when images are being uploaded in the
          language then the frontend will customise. After deleting the
          per-language customized image, and if there are images uploaded in
          en_US, then frontend will display the one in en_US instead.
        </div>-->
        <PhotoGroup
          ref="photo_group"
          :activity-id="activity_id"
          :cur-lang="edit_lang"
          :ref-lang="ref_lang"
          :list="photo_list"
          :need-order="true"
          :need-desc="true"
          :need-alt="isEMpage"
          :need-custom-size="true"
          :need-set-no-crop-banner="false"
          :need-display-as-banner="false"
          :allow-add="allow_add_and_remove"
          :allow-replace="false"
          :allow-remove="allow_add_and_remove"
          :list-all="list_all_format"
          :cur-lang-only.sync="cur_lang_only"
          :show-cur-lang-only="false"
          :allow-download-image="false"
          :is-single="true"
          :need-valid-desc="false"
          :limit="limit"
          :size-conf="sizeConf"
          @copy-photo="copyPhoto"
        />
      </a-form-model-item>
    </a-form-model>
  </div>
</template>
<script>
// activity  guide
//   am: photo desc  , photo alt 
//   em: photo desc  , photo alt 
import PhotoGroup from '../photo-group'
import { getAllImage } from './api'

export default {
  name: 'PhotoGuide',
  components: { PhotoGroup },
  inject: ['activity_id', 'edit_lang', 'ref_lang', 'extra_id', 'extra_type'],
  provide: function() {
    return {
      updateData: this.updateData
    }
  },
  // v-model
  model: {
    prop: 'data',
    event: 'change'
  },
  props: {
    // 
    listAll: {
      type: Array,
      default: () => []
    },
    //! attr_values 
    data: {
      type: Array,
      default: () => []
    },
    //! 
    // eslint-disable-next-line
    refData: {
      type: Array,
      default: () => []
    },
    // 
    sizeConf: {
      type: Object,
      default: () => ({
        width: 1295,
        height: 720
      })
    },
    //! widget 
    ui: {
      type: Object,
      default: () => ({})
    },
    //! 
    required: {
      type: Boolean,
      default: false
    },
    // 
    languageAll: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      form: {},
      // 
      cur_lang_only: false,
      //  list
      photo_list: [],
      // , 
      list_all_format: []
    }
  },
  computed: {
    isEMpage() {
      return document.URL.includes('/act/content/')
    },
    limit() {
      //! 
      return Number(_.get(this.ui, 'ui_conf.config.img_upload_limit') || 1000)
    },
    //! 
    // ...mapGetters({ all_language: "getLanguage" }),
    // 
    all_allow_language() {
      return (this.languageAll.filter((v) => v.status !== 0) || []).map(
        (v) => v.language
      )
    },
    //! ,  or 
    allow_add_and_remove() {
      let all = this.all_allow_language
      //! , , /
      if (all.length === 1 && all[0] === this.edit_lang) {
        return true
      }
      //! , /
      if (all.length > 1 && this.edit_lang === 'en_US') {
        return true
      }
      // 
      return false
    }
  },
  watch: {
    data: {
      immediate: true,
      handler(next, old) {
        if (!_.isEqual(next, old)) {
          this.getCurLangPhoto()
        }
      }
    }
  },
  async created() {
    this.getAllImage()
  },
  mounted() {
    this.$emit('widget-mounted')
    this.getCurLangPhoto()
  },
  methods: {
    // getCurLangPhoto
    async getCurLangPhoto() {
      // 
      //! value, 
      this.photo_list = this.data.map((v, i) =>
        this.formatPhotoConfig(v, i, v.id)
      )
    },
    // , value
    formatPhotoConfig(config, i, id) {
      let v = {
        ...(typeof config.value === 'object'
          ? config.value
          : JSON.parse(config.value || '{}'))
      }
      return {
        ...v,
        id,
        create: config.create || false,
        alt: v.alt_multilang || '',
        desc: v.desc_multilang || '',
        name: v.url || '',
        display_order: config.order || i + 1,
        order: config.order || i + 1,
        height: v.height || '',
        width: v.width || '',
        image: [
          {
            ...v,
            height: v.height || '',
            width: v.width || '',
            name: v.url || '',
            pre_process: '',
            size_type: 'ORIGIN'
          },
          {
            ...v,
            height: v.height || '',
            width: v.width || '',
            name: v.url || '',
            pre_process: '',
            size_type: 'DESKTOP'
          },
          {
            ...v,
            height: v.height || '',
            width: v.width || '',
            name: v.url || '',
            pre_process: '',
            size_type: 'MOBILE'
          }
        ]
      }
    },
    //! 
    async getAllImage() {
      let [res] = await getAllImage({
        extra_id: this.extra_id,
        extra_type: this.extra_type,
        widget_id: this.ui.id
      })
      // 
      if (res) {
        //! "",  value 
        let list_all_format = []
        res.free_text_language_groups.forEach((v) => {
          //! value , ? 
          let list = v.free_text_list.filter((v) => v.value)
          if (list) {
            list_all_format.push({
              language_type: v.language,
              list: list.map((v, i) =>
                this.formatPhotoConfig(v, i, v.attr_value_id)
              )
            })
          }
        })
        this.list_all_format = list_all_format
      }
      //  else {
      // this.$message.error("Fail to get Images");
      // }
    },
    //! (), ,  id
    copyPhoto(images) {
      let photo_list = [...this.photo_list]
      images.forEach((v) => {
        //! id
        let existIndex = photo_list.findIndex((photo) => photo.id === v.id)
        // , 
        if (existIndex > -1) {
          v.display_order = existIndex + 1
          photo_list[existIndex] = v
          return
        }
        v.display_order = photo_list.length + 1
        //! , 
        photo_list.push(v)
      })
      this.photo_list = photo_list
    },
    //! 
    getList() {
      return this.$refs.photo_group.getList()
    },
    //! 
    validateForm() {
      return new Promise((resolve) => {
        this.$refs.form.validate(resolve)
      })
    },
    //! 
    getFormatParams() {
      let params = {}
      let cur_list = _.cloneDeep(this.getList())
      // cur_list = _.sortBy(cur_list, "order");
      params = cur_list.map((v) => {
        let obj = {
          id: +v.id || 0,
          create: v.create || false, // ,
          order: +v.display_order,
          value: JSON.stringify({
            alt_multilang: v.alt,
            desc_multilang: v.desc,
            height: v.height,
            width: v.width,
            url: v.name
          })
        }
        //  value 
        if (!v.name) {
          obj.value = ''
        }
        return obj
      })
      return params
    },
    //  v-model
    updateData() {
      if (this.$refs.photo_group) {
        this.$emit('change', this.getFormatParams())
      }
    },
    // 
    getRules() {
      // (), , 
      let validation = [
        {
          validator: (rule, value, callback) => {
            let res = this.$refs.photo_group.validateFields()
            res.valid ? callback() : callback(new Error(res.message))
          }
        }
      ]
      if (this.required) {
        validation.push({
          required: true,
          validator: (rule, value, callback) => {
            let res = this.$refs.photo_group.validateLength()
            res.valid ? callback() : callback(new Error(res.message))
          }
        })
      }
      return validation
    }
  }
}
</script>
<style lang="scss">
.photo-guide {
  .ant-form-item-control-wrapper {
    width: 100%;
  }
}
</style>
